.App {
  /* text-align: center; */
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@media (min-width: 425px) {
  .bodyclass {
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    overflow: scroll;
    /* background-color: black; */
  }
  textarea {
    height: 500px;
    width: 400px;
    border: 2.5px solid black;
    border-radius: 14px;
    font-size: 24px;
    padding: 10px;
    margin: -3px;
    overflow: scroll;
    color: black;
  }
  #source {
    min-width: 400px;
    min-height: 520px;
    max-width: 100vw;
    border: 2.5px solid black;
    border-radius: 14px;
    font-size: 24px;
    padding: 10px;
    margin: -2px;
    overflow: scroll;
    display: flex;
    align-items: center;
    justify-content: center;
  }
 

  .scopy {
    min-width: 400px;
    min-height: 520px;
    max-width: 100vw;
    border-radius: 14px;
    font-size: 24px;
    padding: 10px;
    margin: -2px;
    overflow: scroll;
  }

  .border {
    border: 2.5px solid black;
    border-radius: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -3px;
  }
  #intsource {
    margin: 10px;
  }
  .htmlView {
    max-height: 490px;
    max-width: 400px;
    min-height: 490px;
    min-width: 400px;
    width: 90%;
    border: 2.3px solid black;
    border-radius: 14px;
    font-size: 24px;
    padding: 5px;
    margin: -3px;
    overflow: scroll;
  }
  #intext{
    max-height: 500px;
    max-width: 500px;
    width: 90%;
    min-height: 490px;
    min-width: 400px;
    border: 2.3px solid black;
    border-radius: 14px;
    font-size:24px;
    padding: 5px;
    margin: -3px;
    overflow:  scroll;
  }
  #btn {
    padding: 10px;
    margin: 10px;
    border: 1px solid black;
    border-radius: 14px;
  }
 
}



#btn:hover {
  background-color: rgb(9, 73, 237);
  /* animation: animate forwards 2000ms linear; */
}


@media (max-width: 425px) {
  .scopy {
    min-width: 200px;
    min-height: 520px;
    max-width: 100vw;
    border-radius: 14px;
    font-size: 24px;
    padding: 10px;
    margin: -2px;
    overflow: scroll;
  }

  .border {
    border: 2.5px solid black;
    border-radius: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -3px;
  }
  #intsource {
    margin: 10px;
  }
  .bodyclass {
    min-height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* flex-wrap: wrap; */
    margin-top: 25px;
  }

  .htmlView {
    max-height: 500px;
    max-width: 85vw;
    min-height: 490px;
    min-width: 200px;
    width: 90%;
    border: 2.3px solid black;
    border-radius: 14px;
    font-size: 24px;
    padding: 0px 10px 10px 10px;
    margin: -3px;
    overflow: scroll;
  }

  #btn {
    padding: 10px;
    margin: 10px;
    border-radius: 14px;
  }
  textarea {
    height: 500px;
    border: 2px solid rgb(0, 0, 0);
    border-radius: 14px;
    font-size: 24px;
    padding: 10px;
    margin: -3px;
    overflow: scroll;
    color: black;
  }

  #intext {
    max-height: 500px;
    max-width: 85vw;
    min-width: 200px;
    width: 90vw;
  }

  #source {
    min-width: 80vw;
    min-height: 520px;
    max-width: 100vw;
    border: 2px solid black;
    border-radius: 14px;
    font-size: 24px;
    padding: 10px;
    margin-top: -3px;
    overflow: scroll;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.changeicon {
  display: flex;
  flex-direction: row;
  margin-bottom: 0px;
}


@keyframes animate {
  0% {
    background-color: aqua;
  }
  50% {
    background-color: red;
  }
  100% {
    background-color: green;
  }
}

#htmlbtn, #htmlbtn1 {
  margin-left: 10px;
}


.btns {
  min-width: 90px;
  width: fit-content;
  margin: 10px 10px 1px 0px;
  padding: 10px;
  padding-bottom: 15px;
  border-radius: 10px 10px 0px 0px;
  border: 2.5px solid black;
}

.borderbottom {
  border-bottom-color: transparent;
}

.hidden {
  display: none;
}

button{
  font-size: 20px;
}
